@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
}

html, body {
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-weight: normal;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  width: 100%;
  max-width: 1204px;
  padding: 0 18px;
  margin: 0 auto;
}

.button {
  display: block;
  cursor: pointer;
  text-decoration: none;
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  transition: all 0.3s ease-in;
  outline: none;
}
.button_blue {
  background-color: #3a4574;
}
.button:hover, .button:focus {
  color: #d3b47f;
  background-color: #37416d;
}

.header {
  position: absolute;
  top: 50px;
  width: 100%;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 450px) {
  .header .container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .header {
    top: 0;
  }
}
@media (max-width: 450px) {
  .header {
    position: static;
    background-color: #3a4574;
    padding-top: 20px;
  }
}

.logo {
  text-decoration: none;
  transition: opacity 0.3s ease-in;
}
.logo:focus {
  outline-color: #d3b47f;
}
.logo:hover {
  opacity: 0.8;
}

@media (max-width: 768px) {
  .logo--img {
    width: 240px;
    height: 45px;
  }
}

@media (max-width: 768px) {
  .navigation {
    order: 3;
  }
}
@media (max-width: 450px) {
  .navigation {
    order: 2;
    justify-self: end;
  }
}

.navigation--list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  width: 454px;
  justify-content: space-between;
}

.navigation--link {
  text-decoration: none;
  color: #ffffff;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  padding: 24px 0;
  transition: all 0.3s ease-in;
}
.navigation--link.active {
  font-weight: 500;
}
.navigation--link:hover {
  color: #d3b47f;
}
.navigation--link:focus {
  outline-color: #d3b47f;
}

@media (max-width: 768px) {
  .navigation--list {
    display: none;
  }
  .navigation--list.open {
    display: block;
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #3a4574;
    z-index: 5;
  }
  .navigation--item {
    border-bottom: 1px solid #d3b47f;
    margin-bottom: 20px;
    text-align: center;
  }
  .navigation--item:first-child {
    padding-top: 20px;
  }
  .navigation--link {
    display: inline-block;
    width: 100%;
    min-height: 44px;
    padding: 12px 24px;
  }
  .navigation--list.open .burger {
    position: relative;
  }
}
.burger {
  display: none;
}
@media (max-width: 768px) {
  .burger {
    width: 44px;
    height: 44px;
    border: none;
    margin: 0;
    padding: 0;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 6;
    cursor: pointer;
  }
  .burger--line {
    display: block;
    width: 18px;
    height: 3px;
    background-color: #3a4574;
    border-radius: 4px;
    position: relative;
    transition: all 0.5s ease-in;
  }
  .burger--line::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 3px;
    border-radius: 4px;
    background-color: #3a4574;
    left: 0;
    top: -9px;
    transition: all 0.5s ease-in;
  }
  .burger:hover .burger--line, .burger:hover .burger--line::before {
    background-color: #d3b47f;
  }
  .burger.show {
    position: relative;
  }
  .burger.show .burger--line,
  .burger.show .burger--line::before {
    background-color: gold;
  }
  .burger.show .burger--line {
    width: 24px;
    transform: rotate(45deg);
  }
  .burger.show .burger--line::before {
    top: 0;
    transform: rotate(-90deg);
  }
}
@media (max-width: 450px) {
  .burger--line, .burger--line::before {
    background-color: #d3b47f;
  }
}

.contacts {
  font-style: normal;
}
@media (max-width: 768px) {
  .contacts {
    order: 2;
  }
}
@media (max-width: 450px) {
  .contacts {
    order: 3;
    grid-column: 1/-1;
    justify-self: center;
  }
}

.contacts--link {
  color: #ffffff;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
  padding: 24px 0;
  transition: all 0.3s ease-in;
  display: flex;
  align-items: center;
}
.contacts--link:hover {
  color: #d3b47f;
}
.contacts--link:focus {
  outline-color: #d3b47f;
}
@media (max-width: 450px) {
  .contacts--link {
    font-size: 24px;
  }
}

.contacts--icon {
  margin-right: 11px;
}
.main {
  flex-grow: 1;
}

.hero {
  background: url("../../img/hero-background.jpg") no-repeat;
  background-size: 115% 105%;
  background-position: top;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  min-height: 100vh;
}
.hero .container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
@media (max-width: 450px) {
  .hero {
    padding-bottom: 20px;
  }
}

.hero--worken-hourse {
  color: #ffffff;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: 2px;
  display: block;
  margin-bottom: 24px;
}

.hero--title {
  color: #ffffff;
  font-weight: 700;
  font-size: 64px;
  line-height: 88px;
  margin: 0;
  margin-bottom: 40px;
}
@media (max-width: 768px) {
  .hero--title {
    font-size: 40px;
    line-height: 60px;
  }
}

.hero--buttons-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .hero--buttons-wrapper {
    flex-direction: column;
  }
}

.hero--button {
  padding: 38px 65px;
}
@media (max-width: 768px) {
  .hero--button {
    order: 2;
    margin-top: 60px;
  }
}
@media (max-width: 375px) {
  .hero--button {
    padding: 30px 55px;
  }
}

.hero--socials {
  width: 267px;
  max-width: 380px;
}

.socials {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.socials--item {
  width: 48px;
  height: 48px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease-in;
}
.socials--item:hover, .socials--item:hover a, .socials--item:focus-within, .socials--item:focus-within a {
  border-color: #d3b47f;
  color: #d3b47f;
}

.socials--link {
  color: #ffffff;
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  outline: none;
  transition: color 0.3s ease-in;
}

.services {
  padding-top: 100px;
  margin-bottom: 90px;
}
.services .container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.services--left {
  width: 42%;
}

.services--list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
  max-width: 469px;
}

.services--item {
  border-bottom: 1px solid #E7E7E7;
  margin-bottom: 20px;
}

.services--date {
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  color: #000000;
  display: block;
  margin-bottom: 10px;
}

.services--content-wrapper {
  margin-bottom: 20px;
  position: relative;
  width: 100%;
  max-width: 468px;
  display: flex;
  align-items: center;
}
.services--content-wrapper::before {
  flex-grow: 1;
  order: 2;
  margin-right: 12px;
  margin-left: 12px;
  border-bottom: 5px dotted #d3b47f;
  content: "";
  width: 100%;
  max-width: 28px;
}

.services--name {
  font-size: 18px;
  line-height: 32px;
  color: #000000;
}
.services--name:first-child {
  order: 1;
}
.services--name:last-child {
  order: 3;
}

.services--right {
  display: flex;
  justify-content: space-between;
  position: relative;
  min-height: 638px;
}
.services--right::before {
  content: "";
  width: 471px;
  height: 502px;
  border: 2px solid #d3b47f;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  top: 55px;
  left: 75px;
}

.services--img {
  display: block;
}

.services--img:first-child {
  align-self: flex-end;
}

@media (max-width: 768px) {
  .services--right {
    display: none;
  }
  .services--left {
    width: 100%;
  }
  .services--list {
    max-width: 100%;
  }
  .services--content-wrapper {
    max-width: 100%;
  }
  .services--content-wrapper::before {
    max-width: none;
  }
}
.slider {
  background-color: #3a4574;
  text-align: center;
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.swiper {
  max-width: 1000px;
}

.slider--content-wrapper {
  width: 100%;
  margin: 0 auto;
  max-width: 785px;
}

.slider--icon {
  margin-bottom: 34px;
}

.slider--title {
  margin-top: 0;
  margin-bottom: 20px;
  color: #d3b47f;
  font-weight: 600;
  font-size: 36px;
  line-height: 40px;
}

.slider--text {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 16px;
  line-height: 40px;
  color: #ffffff;
}

.slider--author {
  color: #d3b47f;
  font-size: 24px;
  line-height: 40px;
  font-weight: 600;
  letter-spacing: 3px;
}

.swiper-button-next,
.swiper-button-prev {
  color: #ffffff !important;
}
.swiper-button-next:hover,
.swiper-button-prev:hover {
  color: #d3b47f !important;
}

@media (max-width: 768px) {
  .slider--content-wrapper {
    max-width: 600px;
  }
}
@media (max-width: 450px) {
  .slider--content-wrapper {
    width: 100%;
  }
  .slider {
    padding: 20px 20px;
  }
  .swiper-button-next,
  .swiper-button-prev {
    display: none !important;
  }
  .slider--title {
    font-size: 28px;
    line-height: 36px;
  }
  .slider--text {
    font-size: 16px;
    line-height: 24px;
  }
  .slider--author {
    font-size: 28px;
    line-height: 36px;
  }
}
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}
.modal-container.modal-container-close {
  display: none;
}

.modal {
  position: relative;
  margin: auto;
  padding: 40px;
  background-color: #ffffff;
  width: 500px;
}

.modal--close-button {
  position: absolute;
  top: 0;
  right: 0;
  width: 44px;
  height: 44px;
  background-color: transparent;
  border: none;
  color: #000000;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #3a4574;
}
.modal--close-button:hover, .modal--close-button:focus {
  background-color: transparent;
}

.modal--title {
  margin: 0;
  margin-bottom: 24px;
  font-size: 32px;
  line-height: 35px;
  font-weight: 700;
  color: #3a4574;
  text-transform: uppercase;
}

.field-group {
  margin: 0;
  margin-bottom: 16px;
}

.field--input {
  margin: 0;
  padding: 8px 14px;
  font-weight: 700;
  background-color: #ffffff;
  border: 2px solid #3a4574;
  width: 100%;
  outline: none;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}
.field--input:hover, .field--input:active, .field--input:focus {
  border-color: #d3b47f;
}
.field--input::placeholder {
  color: #E7E7E7;
}

.field--textarea {
  width: 100%;
  outline: none;
  border: 2px solid #3a4574;
  resize: none;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  margin: 0;
  padding: 8px 14px;
}
.field--textarea:hover, .field--textarea:active, .field--textarea:focus {
  border-color: #d3b47f;
}
.field--textarea::placeholder {
  color: #E7E7E7;
}

.modal--form-button {
  width: 100%;
  height: 44px;
  padding: 0;
  border: none;
  background-color: #3a4574;
  color: #ffffff;
}